<template>
  <v-data-table
    :headers="headers"
    :items="desserts"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title>Mes demandes </v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >Nouvelle demande
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="12"
                  >
                    <v-text-field
                      v-model="editedItem.sujet"
                      label="Sujet"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="12"
                
                  >
                    <v-textarea
                      v-model="editedItem.descri"
                      label="Description"
                      outlined
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
  </v-data-table>
</template>
<script>
import Axios from 'axios'
  export default {
    data: () => ({
      etudiant: '',
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Sujet',
          align: 'start',
          sortable: false,
          value: 'sujet',
        },
        { text: 'Status', value: 'status' },
        { text: 'Description', value: 'descri' },
        { text: 'Date demande ', value: 'date' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        id:'',
        id_etd:'',

        sujet: '',
        status:'',

        descri: '',
        date: '',
      },
      demandes:[],
      defaultItem: {
           id:'',
        id_etd:'',
        sujet: '',
        status:'',
        descri: '',
        date: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
         Axios.get(
                "http://localhost:3000/demande"
              ).then((res) => {
                res.data.forEach((item) => {
                if (localStorage.id == item. id_etd )  {
                     this.desserts.push({
                        id:item.id,
                        id_etd:localStorage.id,
                        sujet: item.sujet,
                        status:item.status,
                        descri: item.description,
                        date: item.date_demande
          });
                  }
                })
              });
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
              Axios.put("http://localhost:3000/demande/"+this.editedItem.id)
            .then(() => {
                     this.dialogDelete = true

            })
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
            Axios.put("http://localhost:3000/demande/"+this.editedItem.id,{
              id:localStorage.id,
            id_etd: localStorage.id,
            sujet:this.editedItem.sujet,
            date_demande: new Date().toJSON().slice(0,10),
            description: this.editedItem.descri,
            status: 'pas encore',
            id_user: localStorage.id,
          })
            .then(() => {
            })
           this.close();
        } else {
          this.message = "L'etudiant "+localStorage.nom+" a ajouter une demande",
          this.editedItem.status='pas encore'
          this.editedItem.date=new Date().toJSON().slice(0,10)
          this.desserts.push(this.editedItem)
          Axios.post("http://localhost:3000/demande/", {
            id_etd: localStorage.id,
            sujet:this.editedItem.sujet,
            date_demande: new Date().toJSON().slice(0,10),
            description: this.editedItem.descri,
            status: "pas encore",
            id_user: localStorage.id,
          })
            .then(() => {
              Axios.post("http://localhost:3000/notification/",{
              id_etd: localStorage.id,
              status:'Non lu',
              date: new Date().toJSON().slice(0,10),
              description: this.message,
              id_resp: '',
              role: "etudiant"
          })
            })
              this.close();
        }
      },
    },
  }
</script>